.apt_detail{

}
.img_detail{
   /* height:300px; */
   border-bottom:1px solid #d2d2d2; 
   display: flex;
   justify-content: center;
}
.img_detail_container{
    position: relative;
}
.test_img{
    width:100%;
    max-width: 600px;
}
.click_point{
    position:absolute;
    background-color: #3ab0e7;
    border-radius: 50px;
    padding:7px 9px;
    cursor: pointer;

    top:10%;
    left:20%;
}
.click_point:hover{
    background-color: #0f9ddf;
}
.click_tooltip{
    background-color: #3ab0e7;
    color:#fff;
    display: block;
    position:absolute;
    margin-top:4%;
    padding:15px 20px;
    border-radius: 5px;
}
.img_detail_like{
    position:absolute;
    top:3%;
    right:3%;
    cursor: pointer;
    z-index: 11;
}
.img_position_box{
    position: relative;
}


.apt_name{
    text-align: center;
    padding: 5px 0;
    background-color: #d2d2d2;
    font-weight: 700;
}
.used_materials{
    height:150px;
    border-bottom:1px solid #d2d2d2;
}



.similar_building{
    height:250px;
}
.similar_building .title, .used_materials .title{
    padding:0 10px;
    font-weight: 700;
    letter-spacing: -0.5px;
}
.similar_img_box{
    display: flex;
    gap:10px;
    padding:10px 15px;
}
.column {
    border: 1px solid #D2D2D2;
    border-radius: 11px;
    cursor: pointer;
    transition: .1s linear;
    width: 160px !important;
    margin-bottom:10px;
}
.column:hover{
    border:1px solid rgb(15,40,90);
}
.column img{
    width:100%;
    min-width: 150px;
    max-width:200px;
    border-radius: 10px 10px 0 0;
}
.similar_img_box{
    max-width:80vw !important;
    margin: 0 auto;
}
.similar_info{
    padding:5px 10px;
}
.similar_info p{
    font-size:13px;
    font-weight: 700;
}
.similar_info span{
    font-size:10px;
}
.slick-slide{
    display: flex !important;
    justify-content: center;
}



